import { PublicClientApplication } from "@azure/msal-browser";
import { AppConfig } from "../../../config/AppConfig";

/*
Note: ( https://docs.microsoft.com/en-us/microsoftteams/platform/tabs/how-to/authentication/auth-tab-aad )
    To get the correct token for Microsoft Teams Free and guest users, 
    it is important that the apps use tenant specific endpoint https://login.microsoftonline.com/**{tenantId}**. 
    You can get tenantId from the bot message or tab context. If the apps use https://login.microsoftonline.com/common, 
    the users will get incorrect tokens and will log on to the "home" tenant instead of the tenant that they are currently signed into.
*/

export const client = new PublicClientApplication({
    auth: {
        clientId: AppConfig.AuthClient.AppId,
        navigateToLoginRequestUrl: false,
    },
    cache: { cacheLocation: "localStorage" },
});
